export const EVENT_IDENTIFY = 1;
export const EVENT_ACCOUNTCREATED = 2;
export const EVENT_DOWNLOADED = 3;
export const EVENT_SEARCHED = 4;
export const EVENT_CHECKOUTSTARTED = 5;
export const EVENT_ORDERCOMPLETED = 6;

export const EVENT_TRACK = 7;

export const EVENT_TRACKLINK = 8;
export const EVENT_TRACKFORM = 9;
export const EVENT_EXPERIMENTVIEWED = 10;
export const EVENT_PRODUCTLISTVIEWED = 11;
export const EVENT_SIGNEDIN = 12;
export const EVENT_INVITESENT = 13;
export const EVENT_PRODUCTSEARCHED = 14;
export const EVENT_SEMANTICCHECKOUTSTARTED = 15;
export const EVENT_SEMANTICORDERCOMPLETED = 16;
export const EVENT_PRODUCTADDEDTOWISHLIST = 17;
export const EVENT_SIGNEDUP = 18;
export const EVENT_ERROR = 19;
export const EVENT_CARTVIEWED = 20;
