export const EVENT_PRODUCTADDED = 22;
export const EVENT_PRODUCTREMOVED = 23;

export const pick = (o, props) =>
  Object.assign({}, ...props.map(prop => ({ [prop]: o[prop] })));

export const cloneDeep = x => merge({}, x);

export const compact = arr => arr.filter(x => x);

export const constant = x => () => x;

export const defaults = (...args) => Object.assign({}, ...args.reverse());

export const defaultsDeep = (target, ...def) =>
  merge(target, ...def.reverse(), cloneDeep(target));

export const isString = x =>
  typeof x === 'string' || (x != null && x.constructor === String);

export const pageDefaults = () => {
  const fullUrl = global.location.href;
  const idx = fullUrl.indexOf('#');
  const url = idx === -1 ? fullUrl : fullUrl.slice(0, idx);
  return {
    path: global.location.pathname,
    referrer: global.document.referrer,
    search: global.location.search,
    title: global.document.title,
    url
  };
};

export { merge };
