import Logger from './logger';
import {
  EVENT_PAGE,
  EVENT_IDENTIFY,
  EVENT_ACCOUNTCREATED,
  EVENT_DOWNLOADED,
  EVENT_ERROR,
  EVENT_SEARCHED,
  EVENT_CHECKOUTSTARTED,
  EVENT_ORDERCOMPLETED,
  EVENT_EXPERIMENTVIEWED,
  EVENT_PRODUCTLISTVIEWED,
  EVENT_SIGNEDIN,
  EVENT_INVITESENT,
  EVENT_PRODUCTSEARCHED,
  EVENT_SEMANTICCHECKOUTSTARTED,
  EVENT_SEMANTICORDERCOMPLETED,
  EVENT_PRODUCTADDEDTOWISHLIST,
  EVENT_SIGNEDUP,
  EVENT_CARTVIEWED,
  EVENT_PAYMENTINFOENTERED,
  EVENT_PRODUCTADDED,
  EVENT_PRODUCTREMOVED
} from './util';

const eventPage = (provider, data) => {
  const { pageType } = data.page;
  provider.logger.info(`EVENT pageview: ${pageType}`);
  provider.analytics.page(pageType, data);
};

const eventIdentify = (provider, data, opt) => {
  let options;
  provider.logger.info('EVENT identify');
  if (opt) {
    const externalIds = [];
    Object.keys(opt).forEach(key => {
      if (key === 'email' || key === 'visitor_id') {
        externalIds.push({
          id: opt[key],
          type: key,
          collection: 'users',
          encoding: 'none'
        });
      }
    });

    if (externalIds.length > 0) {
      options = {
        externalIds
      };
    }
  }

  provider.analytics.identify(data.userId, data.traits, options);
};

const eventAccountCreated = (provider, data) => {
  provider.logger.info('EVENT account created');
  provider.analytics.track('accountCreationSuccess', data);
  provider.analytics.track('accountCreationSuccess-iframe', data);
};

