const otherwise = (provider, payload, data) => {
  provider.logger.info(`EVENT track: ${payload.name}`);
  provider.analytics.track(payload.name, data);
};

export default class SegmentProvider {
  constructor() {
    this.providerLoaded = false;
    this.tagCollectorLoaded = false;
    this.tagCollectorUrl = null;
    this.analytics = null;
    this.logger = Logger.getLogger();
  }

  /* eslint-disable */
  load(sourceId) {
    const myClass = this;
    return new Promise((resolve, reject) => {
      var analytics = (window.analytics = window.analytics || []);

      if (!analytics.initialize && analytics !== myClass) {
        if (analytics.invoked) {
          const err = new Error('Segment snippet included twice.');
          myClass.logger.error(err.message);
          reject(err);
        } else {
          analytics.invoked = !0;

          analytics.load = function(t, e) {
            var n = document.createElement('script');
            n.type = 'text/javascript';
            n.async = true;
            n.src =
              'https://cdn.segment.com/analytics.js/v1/' +
              t +
              '/analytics.min.js';
            n.onload = function() {
              myClass.logger.debug('segment has been loaded');
              myClass.providerLoaded = true;
              myClass.analytics = window.analytics;
              resolve();
            };
            var a = document.getElementsByTagName('script')[0];
            a.parentNode.insertBefore(n, a);
            analytics._loadOptions = e;
          };

          analytics.SNIPPET_VERSION = '4.1.0';
          analytics.load(sourceId);
        }
      } else {
        reject(new Error('Segment already initialized'));
      }
    });
  }
  /* eslint-enable */

  setupTagCollector(tagCollectorUrl) {
    if (this.tagCollectorLoaded) {
      this.logger.warn('tag collector integration has already been setup.');
      return;
    }

    this.tagCollectorUrl = tagCollectorUrl;

    if (!this.providerLoaded) {
      this.tagCollectorUrl = tagCollectorUrl;
      return;
    }

    const sendRequest = payload => {
      const req = new XMLHttpRequest();
      req.open('POST', this.tagCollectorUrl, true);
      req.withCredentials = true;
      req.send(payload);
    };

    // TODO: come back and fix this mehtod so that we are returning a new
    //  result to clean up lint issues
    const normalizeMsg = (msg, type) => {
      if (!msg.timestamp) msg.timestamp = new Date(); // eslint-disable-line no-param-reassign
      if (!msg.type) msg.type = type; // eslint-disable-line no-param-reassign
      return msg;
    };

    const tagCollectorPageHandler = (category, name, properties, options) => {
      const msg = this.analytics.normalize({
        properties,
        category,
        options,
        name
      });
      normalizeMsg(msg, 'page');
      sendRequest(window.JSON.stringify(msg));
    };

    const tagCollectorTrackHandler = (event, properties, options) => {
      const msg = this.analytics.normalize({
        event,
        properties,
        options
      });
      normalizeMsg(msg, 'track');
      sendRequest(window.JSON.stringify(msg));
    };

    const tagCollectorIdentifyHandler = (id, traits, options) => {
      const msg = this.analytics.normalize({
        id,
        traits,
        options
      });
      normalizeMsg(msg, 'identify');
      sendRequest(window.JSON.stringify(msg));
    };

    this.analytics.on('page', tagCollectorPageHandler);
    this.analytics.on('track', tagCollectorTrackHandler);
    this.analytics.on('identify', tagCollectorIdentifyHandler);
    this.tagCollectorLoaded = true;
  }

  handleMessage(name, payload) {
    const { data } = payload || {};
    const opt = data.opt || {};
    const events = {
      [EVENT_PAGE]: eventPage,
      [EVENT_IDENTIFY]: eventIdentify,
      [EVENT_ACCOUNTCREATED]: eventAccountCreated,
      [EVENT_DOWNLOADED]: eventDownloaded,
      [EVENT_ERROR]: eventError,
      [EVENT_SEARCHED]: eventSearched,
      [EVENT_CHECKOUTSTARTED]: eventCheckoutStarted,
      [EVENT_ORDERCOMPLETED]: eventOrderCompleted,
      [EVENT_EXPERIMENTVIEWED]: eventExperimentVieved,
      [EVENT_PRODUCTLISTVIEWED]: eventProductListViewed,
      [EVENT_SIGNEDIN]: eventSignedIn,
      [EVENT_INVITESENT]: eventInviteSent,
      [EVENT_PRODUCTSEARCHED]: eventProductSearched,
      [EVENT_SEMANTICCHECKOUTSTARTED]: eventSemanticCheckoutStarted,
      [EVENT_SEMANTICORDERCOMPLETED]: eventSemanticOrderCompleted,
      [EVENT_PRODUCTADDEDTOWISHLIST]: eventProductAddedToWishlist,
      [EVENT_SIGNEDUP]: eventSignedUp,
      [EVENT_CARTVIEWED]: eventCartViewed,
      [EVENT_PAYMENTINFOENTERED]: eventPaymentInfoEntered,
      [EVENT_PRODUCTADDED]: eventProductAdded,
      [EVENT_PRODUCTREMOVED]: eventProductRemoved
    };
    return typeof events[name] === 'function'
      ? events[name](this, data, opt)
      : otherwise(this, payload, data);
  }
}
